.hp-main-container {
  .hp-header {
    background-color: #51b3e0;
    display: flex;
    position: relative;
    margin-bottom: 400px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 100px;
    height: 500px;
    text-align: center;
    color: rgb(252, 247, 247);
    .hp-title {
      margin-block-start: 0.67em;
      margin-block-end: 0.67em;
      font-size: 2.5rem;
      font-weight: 700;
      line-height: 1.3;
      width: 25ch;
    }
    .hp-subtitle {
      line-height: 1.5;
      font-weight: 600;
      font-size: 1.25rem;
      margin-bottom: 2rem;
    }
    .hp-chart {
        position: absolute;
        bottom: -362px;
    }
    .hp-start-btn {
      button {
        background-color: #51b3e0;
        border-color: #fff;
        &:hover {
          background-color: #ffffff;
          color: #51b3e0; 
        }
      }
    }
  }
  .hp-first-section {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .hp-first-section {
      padding: 24px;
    .hp-section-title {
      margin-block-start: 0.83em;
      margin-block-end: 0.83em;
      margin: 0px 0px 10px;
      font-size: 40px;
      font-weight: 500;
      color: rgb(15, 50, 86);
      text-align: center;
    }
    .hp-features {
        display: flex;
        padding-top: 20px;
        padding-bottom: 100px;
        flex-direction: column;
        justify-content: space-between;
    }
    .hp-feature-icon {
        margin-top: -8px;
        flex-shrink: 0;
        width: 72px;
        height: 72px;
        margin-right: 28px;
        background-color: rgb(255, 255, 255);
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        box-shadow: rgba(218, 225, 233, 0.557) 0px 8px 16px;
    }
    .hp-section-subtitle {
        color: rgb(144, 161, 184);
        margin-block-start: 1em;
        margin-block-end: 1em;    
    }
    .hp-feature-card {
      display: flex;
      flex-wrap: wrap;
    }
    .hp-feature-title {
        margin-bottom: 12px;
        font-size: 20px;
        font-weight: 500;    
        margin-block-start: 0.83em;
        margin-block-end: 0.83em;    
    }
    .hp-feature-subtitle {
        width: 30ch;
        line-height: 1.6;
    color: rgb(131, 143, 160);
    }
    .hp-feature {
        display: flex;
        align-items: center;
    }
  }
}
.loader {
  width: 100%;
  display: flex;
  background-color: #ffffff;
  justify-content: center;
  align-items: center;
  padding: 100px;
  height: 100vh;
}
